.modal_portal {
    position: sticky;
    z-index: 3;
}

.main_block {
    display: grid;
    width: 100%;
    height: 100%;
}

.content_block {
    position: relative;
    top: 0;
    left: 0;
    width: 35rem;
    height: 35rem;
    border-radius: 10px;
    display: grid;
    justify-self: center;
    align-self: center;
    background-color: #F0EDE5;
}

.button_block {
    display: flex;
    justify-content: space-around;
}

.button_block button {
    width: 200px;
    height: 50px;
}

.exit_image {
    position: absolute;
    width: 20px;
    height: 20px;
    top: 4px;
    right: 6px;
    cursor: pointer;
    transition: 0.5s;
}

.estateInfo {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    grid-gap: 1rem;
    margin: 0 30px;
}